
html{color:#000;overflow-y:scroll;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%
}
html *{outline:none;padding:0;margin:0;border:0;-webkit-text-size-adjust:none;-webkit-tap-highlight-color:transparent
}
html,body{height:100%;font-family:'MicroSoft Yahei','Georgia';color:rgba(0,0,0,0.6)
}
body{width:100%;background-color:#efefef;overflow-x:hidden
}
input,select,textarea{font-size:100%
}
table{border-collapse:collapse;border-spacing:0
}
ol,ul{list-style:none
}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:500
}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline
}
sup{top:-0.5em
}
sub{bottom:-0.25em
}
ins,a,a:active,a:hover,a:visited,a:-webkit-any-link{text-decoration:none;color:rgba(0,0,0,0.6)
}
em{font-style:normal
}
*{box-sizing:content-box;line-height:1.5
}
html,body{height:auto
}
[v-cloak]{display:none
}
textarea{-webkit-appearance:none !important
}
.protocol>p{line-height:38px !important
}
#appBody{position:relative;width:100%;height:auto
}
#appBody .loading{position:fixed;top:5px;left:50%;text-align:center;z-index:0
}
#appBody .loading .rotate{display:inline-block;width:38px;height:36px;animation:rotate 1s linear 0.1s infinite;-webkit-animation:rotate 1s linear 0.1s infinite
}
#appBody .loading .rotate img{display:inline-block;width:100%
}
.info-title{box-sizing:border-box;width:100%;height:92px;line-height:110px;padding-left:35px;font-size:20px;color:rgba(0,0,0,0.6)
}
.info-title::before{display:inline-block;content:'';width:12px;height:14px;background:url("./assets/images/info.png") no-repeat 0 0;background-size:100%;margin-right:20px
}
.button-group{position:relative;width:100%;text-align:center;margin-top:60px;font-size:0
}
.button-group .button-item{width:180px;height:65px;font-size:16px;color:rgba(0,0,0,0.9);cursor:pointer;transition:all 0.3s
}
.button-group .button-item.yes{background:url("./assets/images/agree_normal.png") no-repeat;background-size:100%
}
.button-group .button-item.yes:active{background:url("./assets/images/agree_active.png") no-repeat;background-size:100%
}
.button-group .button-item.dis{background:url("./assets/images/white_normal.png") no-repeat;background-size:100%;margin-right:30px
}
.button-group .button-item.dis:active{background:url("./assets/images/white_active.png") no-repeat;background-size:100%
}
.button-group .button-item em{vertical-align:10px
}
@-webkit-keyframes rotate{
0%{-webkit-transform:rotate(0);transform:rotate(0)
}
50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)
}
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}
@keyframes rotate{
0%{-webkit-transform:rotate(0);transform:rotate(0)
}
50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)
}
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}



